<script>
import {
  BButton,
  BForm,
  BFormGroup,
  BFormInput,
  BFormSelect,
  BRow,
  BCol,
  BCard,
  BInputGroup,
  BInputGroupAppend,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "axios";

export default {
  components: {
    BFormSelect,
    BButton,
    BForm,
    BFormGroup,
    BFormInput,
    BRow,
    BCol,
    BCard,
    BInputGroup,
    BInputGroupAppend,
  },
  data() {
    return {
      departments: [],
      department: "",
      sub_department: "",
      job_level: "",
      job_position: "",
      employee_type: "",
    };
  },
  directives: {
    Ripple,
  },
  mounted() {
    axios({
      method: "get",
      url: "http://sw-api.hracademy.id/api/department?with_responsibilities=1",
      headers: {
        Authorization: "Bearer " + localStorage.getItem("sw_token"),
      },
    })
      .then((response) => {
        this.departments = response.data.result.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
  computed: {},
  methods: {},
};
</script>
